.detail__title {
  text-align: center;
  margin-top: 100px;
  font-size: 50px;
}

.detail__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 50px;
  max-width: 60%;
  font-weight: 300;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detail__container img {
  max-height: 400px;
}

.detail__container__summary {
  padding-left: 100px;
}

.detail__container__summary h2 {
  font-size: 35px;
}

.detail__container__summary p {
  font-size: 25px;
}
